.hero-container > img {
    object-fit: cover;
    width: 100%;
    height:100%;
    /* position: fixed; */
    z-index: -1;
}
.hero-container > video {
    object-fit: cover;
    width: 100%;
    height:100%;
    /* position: fixed; */
    z-index: -1;
}
.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -550px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

.hero-container > p {
    margin-top: 8px;
    text-align: center;
    color: #fff;
    font-size: 32px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

@media screen and (max-width: 960px) {
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -450px;
    }
}

@media screen and (max-width: 760px) {
    .hero-container > h1 {
        font-size: 50px;
        margin-top: -500px;
    }
    .hero-container > p{
        font-size: 30px;
    }
}